import React, { Component } from 'react';

class Iframe extends Component {
    constructor(props){
        super(props)
        this.state= {
            response : "Loding...",
            animate : 0
        }
        this.handleAnimate=this.handleAnimate.bind(this);
        this.handleHtml(this.props.active,0)
    }
    
    componentWillReceiveProps(next){
        if (this.props.active>next.active){
            this.setState({
                animate : 1
            })
            this.handleHtml(next.active,3)
        }
        else if(this.props.active<next.active){
            this.setState({
                animate : 2
            })
            this.handleHtml(next.active,4)
        }
    }

    handleHtml(where,direction){
        fetch("/JumpToOpenSource/documents/"+where+".html").then((res) =>{
            let rtn = res.text();
            if(res.status===200){
                return rtn;
            }
            else{
                return null
            }
        }).then((res) => {
            if(res){
                this.setState({
                    response : res,
                    animate : direction
                })
            }
        })
    }

    handleAnimate(){
        this.setState({
            animate:0
        })
    }

    render() {
        return (
            <div className= {
                this.state.animate === 0?
                    ("maintext")
                    :
                    (this.state.animate <=2 ?
                        (this.state.animate === 1 ? 
                            "maintext-rightout"
                        :
                            "maintext-leftout"
                        ) 
                        :
                        (this.state.animate ===3 ? 
                            "maintext-rightin"
                        :
                            "maintext-leftin"
                        )
                    )
            } onAnimationEnd={this.handleAnimate} dangerouslySetInnerHTML={ {__html: this.state.response} }/>
        );
    }
}

export default Iframe;